var keyboard = Keyboard();
keyboard.init();
function Vector(x, y) {
    return {
        x: x,
        y: y,
    };
}
var Bullet = function (x, y) {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var y = y;
    var x = x;
    var update = function () {
        if (keyboard.SPACE()) {
            y--;
        }
    };
    var draw = function () {
        context.beginPath();
        context.moveTo(x, y);
        context.lineTo(x, 50);
        context.stroke();
    };
    return {
        draw: draw,
        update: update,
    };
};
var Player = function (x, y) {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var radio = 20;
    var width = radio * 2;
    var defaults = {
        x: x,
        y: y,
    };
    var x = defaults.x;
    var y = defaults.y;
    var angle = 0;
    var velocity = 0;
    var aceleration = 1;
    var update = function () {
        if (keyboard.LEFT()) {
            if (angle <= 0) {
                angle = 0;
            }
            else {
                angle = angle - 1 * velocity;
            }
        }
        if (keyboard.RIGHT()) {
            if (angle >= 360) {
                angle = 360;
            }
            else {
                angle = angle + 1 * velocity;
            }
        }
        if (keyboard.DOWN()) {
            velocity--;
        }
        if (keyboard.UP()) {
            velocity++;
        }
    };
    var draw = function () {
        context.save();
        context.translate(x, y);
        context.rotate((angle * Math.PI) / 180);
        context.beginPath();
        context.arc(0, -radio, 5, 0, 2 * Math.PI);
        context.stroke();
        context.beginPath();
        context.arc(0, 0, radio, 0, 2 * Math.PI);
        context.restore();
    };
    return {
        draw: draw,
        update: update,
    };
};
function GameState() {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var player1;
    var init = function () {
        player1 = Player(100, 100);
    };
    var update = function () {
        player1.update();
    };
    var draw = function () {
        player1.draw();
    };
    init();
    return {
        draw: draw,
        update: update,
    };
}
function Keyboard() {
    var keys = {};
    var UP, LEFT, RIGHT, DOWN, SPACE;
    var init = function () {
        window.addEventListener("keydown", function (e) {
            keys[e.keyCode] = true;
        }, false);
        window.addEventListener("keyup", function (e) {
            keys[e.keyCode] = false;
        }, false);
    };
    var update = function () {
        SPACE = keys[32];
        UP = keys[38];
        LEFT = keys[37];
        RIGHT = keys[39];
        DOWN = keys[40];
    };
    return {
        update: update,
        init: init,
        UP: function () { return UP; },
        LEFT: function () { return LEFT; },
        RIGHT: function () { return RIGHT; },
        DOWN: function () { return DOWN; },
        SPACE: function () { return SPACE; },
    };
}
window.onload = function () {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var game = GameState();
    (function draw() {
        window.requestAnimationFrame(draw);
        context.clearRect(0, 0, canvas.width, canvas.height);
        context.rect(0, 0, 300, 300);
        context.stroke();
        keyboard.update();
        game.update();
        game.draw();
    })();
};
